<script>
  import { onMount } from "svelte";
  import Split from "split.js";
  import NavLoader from "./Nav-loader.svelte";
  import DocsLoader from "./Docs-loader.svelte";

  let initializing = true;
  let searchVal = "";
  let searchInput;
  let selectedNav = "";
  let menuVisible = true;
  var screen = window.matchMedia("(max-width: 799px)");
  $: isMobile = screen.matches;
  let searchVisible;
  let htmlSource;
  let navSource = [];
  $: nav = navSource.filter((nav) => nav.id?.includes(searchVal.toLowerCase()));
  let docsSource = [];
  $: docs = docsSource.filter((doc) => doc.id?.includes(searchVal.toLowerCase()));
  $: meta = {};

  let isFocused = false;
  const onInputFocus = () => (isFocused = true);
  const onInputBlur = () => (isFocused = false);
  let highlightedDoc = null

  screen.addEventListener("change", (e) => {
    if (e.matches) {
      if (menuVisible) menuVisible = false;
      isMobile = true;
      destroySplit();
    } else {
      isMobile = false;
      menuVisible = true;
      setTimeout(() => {
        initSplit();
      });
    }
  });

  const htmlUrl = "https://cors.bridged.cc/https://www.tvpaint.com/doc/tvpaint-animation-11/george-commands";

  // const url =
  //   "https://raw.githubusercontent.com/AsafAgranat/asafagranat.github.io/master/files/TVP_george_ref.txt";

  onMount(async () => {
    const htmlRes = await fetch(htmlUrl)
      .then((res) => {
        return res.text();
      })
      .then((str) => {
        processFetchResponse(str);
        initSplit();
      });
  });

  function processFetchResponse(str) {
    const substr = '<p class="code">';
    const position = str.indexOf(substr);

    htmlSource = str
      .slice(position + substr.length)
      .split(/------|------/g)
      .filter((val) => val !== "");

    let navNodes = htmlFromString(htmlSource[0]);
    navNodes.forEach((entry, i) => {
      function setBrief(id, index, text) {
        return navSource.find((item) => item.id === id) || navNodes[i + 2]?.innerText === id;
      }

      if (entry.nodeName === "A") {
        let navItem = {
          index: i / 2,
          id: entry.innerText,
          brief: navNodes[i + 1].textContent,
          multiple: setBrief(entry.innerText, i, navNodes[i + 1].textContent),
        };

        navSource = [...navSource, navItem];
      }
    });

    let docsNodes = htmlSource.slice(1);
    docsNodes.forEach((entry, i) => {
      const node = htmlFromString(entry);

      function nodesToString(nodelist) {
        let [...nodes] = nodelist;
        let arr = nodes.slice(nodes[0].nodeName === "#text" ? 2 : 1);
        let text = nodelistFromArray(arr);
        return text
          .replace("[PARAMETERS]", "<h3>Parameters</h3>")
          .replace("[ERROR]", "<h3>Error</h3>")
          .replace("[WARNING]", "<h3>Warning</h3>")
          .replaceAll("[RETURN]", "<h3>Return</h3>")
          .replaceAll("[COMMENT]", "<h3>Comment</h3>");
      }

      let doc = {
        index: i,
        id: node[node[0].nodeName === "#text" ? 1 : 0].innerText,
        ...(node[0].nodeName === "#text" && { version: node[0].textContent }),
        content: nodesToString(node),
      };

      if (docsNodes.length === i + 1) {
        let footer = htmlFromString(entry);
        footer.forEach((node) => {
          if (node.nodeName === "FOOTER") {
            let [...children] = node.childNodes;
            meta = {
              updated: children
                .find((el) => el.className === "last-update")
                .innerText.split(":")
                .pop()
                .trim(),
              copyright: children.find((el) => el.className === "copyright").innerText.trim(),
            };
          }
        });
        initializing = false;
      } else {
        docsSource = [...docsSource, doc];
      }
    });
  }

  var splitInstance = {};

  function initSplit() {
    splitInstance = Split(["#nav", "#main"], {
      sizes: [25, 75],
      minSize: [100, 300],
    });
  }

  function destroySplit() {
    splitInstance.destroy();
  }

  function htmlFromString(htmlString) {
    var div = document.createElement("div");
    div.innerHTML = htmlString.trim();
    return div.childNodes;
  }

  function nodelistFromArray(arrayOfNodes) {
    var div = document.createElement("div");
    arrayOfNodes.forEach((item) => div.appendChild(item));
    return div.innerHTML;
  }

  function clearInput() {
    searchVal = "";
    if (isMobile) searchVisible = false;
  }

  function toggleMenu() {
    menuVisible = !menuVisible;
  }

  function handleKeydown(event) {
    if (isMobile) searchVisible = true;
    setTimeout(() => {
      if (document.activeElement.id !== "searchInput" && event.keyCode >= 65 && event.keyCode <= 90) {
        searchVal = event.key;
        searchInput.focus();
      }
    });
  }

  function handleMainClick() {
    if (!isMobile) return;
    if (menuVisible) {
      toggleMenu();
    }
  }

  function handleNavClick(name) {
    if (!isMobile) return;
    selectedNav = name;
    toggleMenu();
  }

  function handleSearchButtonClick() {
    searchVisible = true;
  }

  function highlightDoc(id, index) {
    highlightedDoc = `${id}_${index}`
    setTimeout(() => {
      highlightedDoc = null
    }, 500);
  }
</script>

<svelte:head>
  <title>Blah</title>
</svelte:head>
<svelte:window on:keydown={handleKeydown} />
<header>
  <div>
    {#if isMobile}
      <button class="menu-button" on:click={toggleMenu}>☰</button>
    {/if}
    <h1 class:is-mobile={isMobile}>TVPaint George Script documentation</h1>
    <!-- <div /> -->
  </div>
  <div>
    {#if !initializing}
      <div class="meta-date">Updated: {meta.updated}</div>
    {/if}
  </div>
</header>
<div class="wrapper">
  {#if menuVisible}
    <nav id="nav" class:is-mobile={isMobile}>
      <div class="nav-inner">
        {#if initializing}
          <div class="nav-loader">
            <NavLoader count={5} />
          </div>
        {:else}
          <div class="search-input-wrapper" class:is-mobile={isMobile}>
            <!-- {#if isMobile && !searchVisible}
              <button class="search-button" on:click={handleSearchButtonClick}>🔍</button>
            {/if} -->
            <!-- {#if !isMobile || (isMobile && searchVisible)} -->
              <input
                type="text"
                bind:value={searchVal}
                bind:this={searchInput}
                class="search-input"
                class:is-mobile={isMobile}
                class:is-busy={searchVal.length}
                class:is-focused={isFocused}
                on:focus={onInputFocus}
                on:blur={onInputBlur}
                id="searchInput"
                placeholder="Start typing"
              />
              <button class="clear-input" on:click={clearInput}>✕</button>
            <!-- {/if} -->
          </div>
        {/if}
        {#each nav as nav}
          <a href={`#${nav.id}_${nav.index}`} class:selected={selectedNav === nav.id} on:click={handleNavClick(nav.id)} title={`${nav.brief}`} on:click={highlightDoc(nav.id, nav.index)}>
            <span class="link-prefix">tv_</span>{nav.id.replace("tv_", "")}
            {#if nav.multiple}
              <span class="link-brief">{nav.brief.replace('["toolmode"]', "")}</span>
            {/if}
          </a>
        {/each}
      </div>
    </nav>
  {/if}

  <main id="main" class:is-blocked={isMobile && menuVisible} on:click={handleMainClick}>
    {#if initializing}
      <div class="nav-loader">
        <DocsLoader count={5} />
      </div>
    {/if}
    {#if docs.length > 0}
      {#each docs as doc}
        <div class="anchor" id={`${doc.id}_${doc.index}`} />
        <article class:is-highlighted={highlightedDoc === `${doc.id}_${doc.index}`}>
          <div class="article-header">
            <h2>
              {doc.id}
            </h2>
            {#if doc.version}<span class="article-version">{doc.version}</span>{/if}
          </div>
          <div class="article-content">
            <section>
              <pre>{@html doc.content}</pre>
            </section>
          </div>
        </article>
      {/each}
      
    {:else if !initializing}
      <div class="no-content">No matching docs</div>
    {/if}
    <div class="meta-copyright">
        {meta.copyright}
      </div>
  </main>
</div>

<style lang="scss">
  :root {
    --header-height: 68px;
  }
  header {
    position: fixed;
    height: var(--header-height);
    padding: 1rem;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    > div {
      display: flex;
      align-items: center;
    }
  }
  h1 {
    font-size: 1.4rem;
    margin: 0;
    &.is-mobile {
      font-size: 1rem;
    }
  }
  .menu-button {
    margin: 0;
    width: 40px;
    margin-right: 1rem;
  }
  .search-input {
    width: 400px;
    margin: 0;
    padding: 0.6em;
    border-radius: 2em;
    padding-left: 1rem;
    max-width: 100%;
    // will-change: width;
    // transition: width 0.5s var(--easeInOutExpo);
    font-weight: 600;
    &.is-mobile {
      width: calc(100vw - 1rem - var(--header-height));
    }
    // &.is-busy,
    // &.is-focused {
    //   width: 320px;
    // }
  }

  .search-input-wrapper {
    position: sticky;
    top: 0;
    align-self: flex-start;
    max-width: 100%;
    padding-left: 1rem;
    margin-bottom: 0.5rem;
    z-index: 1;
    &.is-mobile {
    //   position: absolute;
    //   right: 0.5rem;
    padding-right: .5rem
    }
  }
  .clear-input {
    position: absolute;
    z-index: 100;
    width: 36px;
    height: 36px;
    margin: 0;
    border: none;
    background: transparent;
    right: 6px;
    &:focus {
      outline: auto;
    }
  }
  .wrapper {
    display: flex;
    height: calc(100%);
    padding-top: var(--header-height);
  }
  nav {
    direction: rtl;
    overflow: auto;
    overflow-x: hidden;
    min-width: 220px;
    height: 100%;
    padding: 0.25rem;
    padding-top: 0;

    .nav-inner {
      display: flex;
      flex-direction: column;
      direction: ltr;
    }

    a {
      padding: 0.5rem 1rem;
      text-decoration: none;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      // color: $blue;
      &:hover,
      &:focus {
        background-color: #eee;
      }
      &:active {
        background-color: #ddd;
      }
    }
    &.is-mobile {
      box-shadow: 40px 0px 35px -35px rgb(0 0 0 / 10%);
    }

    .link-prefix {
      opacity: 0.3;
    }

    .link-brief {
      font-weight: initial;
      color: initial;
      font-style: italic;
      opacity: 0.8;
      white-space: pre;
    }
  }
  main {
    overflow-x: hidden;
    padding: 0 1rem 2rem 1rem;
    &.is-blocked {
      // pointer-events: none;
      user-select: none;
    }
  }
  .anchor {
    margin-top: 10px;
    margin-bottom: -10px;
    height: 10px;
  }
  article {
    padding: 1rem 2rem;
    margin-top: 1rem;
    box-shadow: 0 0 15px 0 rgb(0 0 0 / 5%);
    background: white;
    position: relative;

    > * {
        position: relative;;
      }

    h3 {
      text-transform: uppercase;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: lightyellow;
      opacity: 0;
      z-index: 0;
      will-change: opacity;      
      transition: opacity 1.5s;
    }
    &.is-highlighted {
      &:before {
        opacity: 1;
        transition: opacity .15s;
      }

      .article-header {
        background: transparent;
        transition: background 0s
      }
    }
  }
  .article-header {
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background: white;
    padding: 1rem 0;
    will-change: background;
    transition: background 0s 2s;
    z-index: 1;
  }
  .article-version {
    background-color: #eee;
    font-weight: 700;
    border-radius: 14px;
    padding: 0 0.5rem;
    margin-left: 1rem;
  }
  .article-content {
    overflow: auto;
  }

  @media screen and (max-width: 799px) {
    nav {
      position: fixed;
      left: 0;
      width: 280px !important;
      max-width: none !important;
      bottom: 0;
      height: calc(100% - 68px);
      background: var(--page-bg);
      z-index: 10;
    }
    main {
      width: 100% !important;
    }

    .menu-button {
      display: block;
    }
  }

  .meta-date {
    padding: 0.5rem 1rem;
    background: #eee;
    display: inline-block;
    border-radius: 1rem;
    font-weight: 600;
  }

  .meta-copyright {
    padding: 2rem;
    margin-top: 2rem;
  }

  .no-content {
    @extend article;
    display: inline-block
  }
</style>
